
$color-blue: #182b89;
$color-lightblue: #3ea6df;
$bg-grey: #d8d8d8;

$transition-2s: all 0.2s ease-in-out;


nav {
    // background-color: rgba(24, 43, 136, 1);
    background: linear-gradient(0deg, rgba(62, 167, 224, 0), rgba(24, 43, 136, 1));
    width: 100%;
    height: 80px;
    max-height: 80px;
    top: 0;
    position: sticky !important;
    z-index: 2000 !important;
}

.nav-bgcolor {
    background: $color-blue;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.navbar-container {
    width: 95%;
    height: 80px;
    max-height: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-container {
    width: 180px;

    & img {
        width: 180px;
    }
}

.nav-menu {
    width: 100%;
    height: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: center;

    & > .nav-item {
        color: #fff;
        height: 65px;
        margin: 0 15px;
        display: flex;
        align-items: center;
        text-align: center;

        & > .nav-links {
            color: #fff;
            height: 65px;
            padding: 30px 30px 0;
            font-size: 14px;
            font-weight: normal;
            font-stretch: normal;
            text-decoration: none;

            &.active {
                font-weight: bold;
                background-color: rgba(255, 255, 255, 0.2);
                backdrop-filter: blur(8px);
                -webkit-backdrop-filter: blur(8px);
            }
        }
    }
}


.nav-acc-dropdown {

    & svg {
        font-size: 24px;
    }

    & .btn {
        padding: 0;
        box-shadow: none;
        transition: $transition-2s;
    }

    & .btn-primary {
        background-color: transparent;
        border: none;
        outline: none;
    }

    & .dropdown-toggle::after {
        display: none;
    }

    & .show>.btn-primary.dropdown-toggle:focus {
        box-shadow: none;
    }

    & .show > .btn-primary.dropdown-toggle {
        background-color: transparent;
        border: none;
    }

    & .nav-acc-dropdown-menu {
        transform: translate(0px, 53px) !important;
        padding: 0 20px;
        border: none; 
        border-radius: 10px;
        // box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);

        & a {
            color: $color-blue;
            font-size: 14px;
            padding: 12px 0;
            &:hover {
                background-color: #fff;
            }
        }
        & .dropdown-linediv {
            border-bottom: 1px solid #d8d8d8;
        }
    }
}



.nav-mob-menu {
    display: none;
}


// login modal
.login-modal {
    backdrop-filter: blur(10px);
    z-index: 2000;

    .modal-dialog {
        max-width: 400px;
    }

    & .modal-content {
        border-radius: 20px;
    }
}

.login-modal-header {
    border: none;
    padding-bottom: 0;

    & .modal-title {
        color: $color-blue;
        font-size: 20px;
        font-weight: bold;

        & p {
            color: #aeaeae;
            font-size: 12px;
            font-weight: normal;
        }
    }
}

.login-modal-body {
    padding: 20px 20px 30px;

    & .login-form-username {
        margin: 0 0 10px;

        & > label {
            color: $color-blue;
            font-size: 14px;
            font-weight: 500;
            margin-bottom: 8px;
        }

        & > input {
            background-color: #fff;
            width: 100%;
            padding: 10px 14px;
            font-size: 12px;
            border: none;
            border-radius: 10px;
            outline: 0;
            box-shadow: 0 0 5px 0 $color-lightblue;
        }
    }

    & .login-form-ps {
        margin: 0 0 10px;

        & > label {
            color: $color-blue;
            font-size: 14px;
            font-weight: 500;
            margin-bottom: 8px;
        }

        & > input {
            background-color: #fff;
            width: 100%;
            padding: 10px 14px;
            font-size: 12px;
            border: none;
            border-radius: 10px;
            outline: 0;
            box-shadow: 0 0 5px 0 $color-lightblue;
        }
    }

    & .login-form-etc {
        display: flex;
        justify-content: space-between;
        margin: 0 0 50px;

        & button {
            color: $color-blue;
            background-color: #fff;
            font-size: 12px;
            font-weight: 500;
            border: none;
            text-decoration: underline;
            transition: $transition-2s;

            &:hover {
                color: $color-lightblue;
            }
        }
    }

    & .login-form-email {
        margin: 0 0 10px;

        & > label {
            color: $color-blue;
            font-size: 14px;
            font-weight: 500;
            margin-bottom: 8px;
        }

        & > input {
            background-color: #fff;
            width: 100%;
            padding: 10px 14px;
            font-size: 12px;
            border: none;
            border-radius: 10px;
            outline: 0;
            box-shadow: 0 0 5px 0 $color-lightblue;
        }
    }

    & .login-alert-message {
        padding: 10px 16px;

        & .login-alert-text {
            font-size: 14px;
            font-weight: 400;
            margin: 0 0 0 10px;
        }
    }
}

.login-buttons {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;

    & button {
        color: #fff;
        background-color: $color-lightblue;
        padding: 14px 0;
        font-size: 12px;
        font-weight: bold;
        border: none;
        border-radius: 10px;
        transition: $transition-2s;
        &:hover {
            background-color: #64b6e3;
        }
    }
}

.modal-backdrop {
    background-color: $color-blue;
}





@media screen and (min-width: 1901px) {

}



@media screen and (max-width: 1024px) {

    nav {
        // background-color: rgba(24, 43, 136, 1);
        background:  rgba(24, 43, 136, 1);
    }


    .wrap-navbar {
        z-index: 100;
    }

    .logo-container img {
        width: 160px;
    }

    .nav-menu {
        display: none;
    }

    .tabl-menu {
        display: initial;
    }

    .nav-account {
        display: grid;
        grid-template-columns: 40px 40px;
        gap: 20px;
        align-items: center;
    }

    .nav-mob-menu {
        display: initial;
    }

    .mob-menu-item {
        & > .menu-icon-burger {
            color: #fff;
            font-size: 30px;
        }
    }

    /* mobile menu offcanvas  */
    .mob-menu-offcanvas {
        display: initial;
        width: 95%;
        height: fit-content;
        z-index: 5;
        margin: 90px auto 0;
        border-radius: 10px;

        & .mob-menu-body {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0;
            padding: 0;
            border-radius: 10px;

            & .offcanvas-link {
                color: $color-blue !important;
                text-decoration: none;
                font-size: 18px;
                padding: 20px 30px;
            }

            & .offcanvas-link.active {
                color: #fff !important;
                background-color: $color-blue;
                font-size: 18px;
                border-radius: 0 ;
            }
        }

        
    }

    .offcanvas-backdrop {
        background-color: $color-blue;
        height: 100vh;
        left: 0;
        position: fixed;
        top: 0;
        width: 100vw;
        z-index: 2 !important;
        margin: 80px 0 0;
        backdrop-filter: blur(10px) !important;
    }
    .offcanvas-backdrop.show, .offcanvas-backdrop.fade {
        opacity: 0.5;
        z-index: 0;
        backdrop-filter: blur(10px);
    }

}




@media screen and (max-width: 576px) {

    .logo-container img {
        width: 140px;
    }


    /* mobile menu offcanvas  */
    .mob-menu-offcanvas {
        width: 95%;
        height: fit-content;
        z-index: 5;
        margin: 90px auto 0;
        border-radius: 10px;

        & .mob-menu-body {
            display: grid;
            grid-template-columns: 1fr;

            & .offcanvas-link {
                font-size: 14px;
                padding: 14px 20px;
            }

            & .offcanvas-link.active {
                font-size: 14px;
            }
        }

        
    }




    // login modals
    .login-modal-body {
        padding: 20px;

        & .login-form-username {
            & > label {
                font-size: 12px;
            }
        }


        & .login-form-email {
            & > label {
                font-size: 12px;
            }
        }


        & .login-form-ps {
            & > label {
                font-size: 12px;
            }
        }

        & .login-form-etc {
            margin: 0 0 20px;
        }
    }
    
    .login-buttons {
        width: 100%;
        gap: 10px;
    }
}