
$color-blue: #182b89;
$color-lightblue: #3ea6df;

$transition-2s: all 0.2s ease-in-out;

// spinner & error message
.LoadingSpinner {
    text-align: center;
    padding: 30px 0;

    & .text-info {
        color: $color-blue !important;
    }
}

// hero
.wrap-cars-hero {
    width: 100%;
    height: 100vh;
    margin: -80px auto 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 1), rgba(24, 43, 136, 0.1),rgba(24, 43, 136, 0.3),rgba(24, 43, 136, 0.5),rgba(24, 43, 136, 0.6), rgba(24, 43, 136, 0.7)), url('../../images/BG-23-02.png');
    background-repeat: no-repeat;
    background-position: top center; 
    background-size: cover;
    display: flex;
    align-items: center;
}

.cars-hero-container {
    width: 50%;
    background-color: rgba(238, 238, 238, 0.15);
    border-radius: 0 500px 500px 0;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    display: grid;
    grid-template-columns: 1fr;
    padding: 140px 0;
    align-items: center;
    justify-content: center;

    & > h1 {
        width: 70%;
        margin: 0 auto 20px;
        color: #fff;
        font-size: 36px;
        font-weight: bold;
        letter-spacing: normal;
        line-height: 1.5;
        display: grid;
        grid-template-columns: 1fr;
        & > span {
            font-size: 50px;
        }
    }

    & > p {
        width: 70%;
        margin: 0 auto;
        color: #fff;
        font-size: 24px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.29;
        letter-spacing: normal;
    }
}



// content
.wrap-cars {
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 90px 0 ;
}

.cars-header {
    width: 31%;
    margin: 0 auto 60px;
    padding: 0 0 0 20px; 
    border-left: 20px solid $color-blue;

    & > h2 {
        color: $color-blue;
        font-size: 24px;
        font-weight: bold;
        letter-spacing: 0.5px;
    }
}

.cars-container-1 {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 90px;
}

// filter

// mobile view
.cars-c-1-item-mobile {
    display: none;
}

// desktop
.cars-c-1-item {
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.3);
    border-radius: 15px;

    & > .cars-c-1-item-header {
        color: #fff;
        background: $color-blue;
        padding: 10px 20px;
        border-radius: 15px 15px 0 0;
        & > span {
            font-size: 18px;
            font-weight: 500; 
        }
    }

    & > .cars-c-1-input-search {
        width: 90%;
        margin: 20px auto 10px;
        border-radius: 10px;
        box-shadow: 0 0 5px 0 rgba(62, 166, 223, 0.3);
        
        & .input-group-text {
            color: #989898;
            background: #fff;
            border: none;
            border-radius: 10px;
        }

        & .form-control {
            color: $color-blue;
            font-size: 13px;
            font-weight: bold;
            padding: 8px 5px 8px 0;
            box-shadow: none;
            transition: none;
            border: none;
            border-radius: 10px;

            &:focus {
                border: none;
            }
        }
        
        & input::-webkit-input-placeholder { /* Edge */
            color: #989898;
            font-size: 11px;
            font-weight: normal;
        }  
        & input:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #989898;
            font-size: 11px;
            font-weight: normal;
        } 
        & input::placeholder {
            color: #989898;
            font-size: 11px;
            font-weight: normal;
        }
    } 

    & > .cars-c-1-item-body {
        padding: 0 10px 30px;
        border-radius: 15px 15px 0 0;
        & > span {
            font-size: 18px;
            font-weight: 500; 
        }
    }
}




// dropdown
.cars-c-1-filter-item {

    & h3 {
        color: $color-blue;
        font-size: 16px;
        font-weight: bold;
        padding: 0 10px 10px;
        // margin: 0 0 10px;
        border-bottom: 1px solid #d9d9d9;
    }

    & .btn {
        color: #989898;
        background-color: transparent;
        width: 100%;
        margin: 0;
        padding: 15px 10px ;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 13px;
        font-weight: normal;
        border: none;
        border-bottom: 1px solid #d9d9d9;
        border-radius: 0;
        overflow-x: hidden;

        &:focus {
            box-shadow: none;
        }

    }

    &.show > .btn-primary.dropdown-toggle {
        color: $color-blue;
        background-color: transparent;
        font-weight: bold;
        border-bottom: 1px solid #d9d9d9;
    }
    &.show > .btn-primary.dropdown-toggle:focus {
        box-shadow: none;
    }

    & .dropdown-menu {
        width: 350px;
        max-height: 500px;
        overflow-y: scroll;
        padding: 15px 10px 10px;
        border: none;
        border-radius: 15px;
        box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.3);
        &::-webkit-scrollbar {
            display: none;
        }
    }
    & .dropdown-menu.show {
        transform: translate(305px, 0px) !important;
    }

    & .dropdown-item {
        padding: 10px ;
        font-size: 13px;
        border-bottom: 1px solid #d9d9d9;
        &:hover {
            color: $color-blue;
            background-color: transparent;
        }
        &:nth-last-child(1) {
            border: none;
        }
        
    }


    & .save-filter-option-btn {
        color: #fff;
        background-color: $color-blue;
        width: 100%;
        margin: 20px auto 0;
        padding: 10px 0;
        display: flex;
        justify-content: center;
        font-size: 13px;
        font-weight: 500;
        outline: 0;
        border: none;
        border-radius: 10px;
        transition: $transition-2s;
        &:hover {
            background-color: #3044a7;
        }
    }

}

.cars-c-1-item-search {
    color: #fff;
    background-color: $color-lightblue;
    width: 90%;
    margin: 20px auto 0;
    padding: 13px 0;
    display: flex;
    justify-content: center;
    font-size: 13px;
    font-weight: 500;
    outline: 0;
    border: none;
    border-radius: 10px;
    transition: $transition-2s;
    &:hover {
        background-color: #64b6e3;
    }
}


// range
.cars-c-1-filter-range {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 10px auto ;

    & input {
        border-radius: 0;
        &:nth-last-child(2) {
            z-index: 5;
        }
        &:nth-last-child(1) {
            margin-left: -5px;
        }
    }
    
}

.cars-c-1-filter-details {
    margin: 0 auto 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    & > p {
        display: grid;
        grid-template-columns: 1fr ;
        color: $color-blue;
        font-size: 14px;
        font-weight: normal;
        & > span {
            font-size: 16px;
            font-weight: bold;
        }
    }
}

.cars-c-1-filter-option {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    & > button {
        color: #fff;
        background-color: $color-lightblue;
        width: 100%;
        padding: 10px 0;
        display: flex;
        justify-content: center;
        font-size: 13px;
        font-weight: 500;
        outline: 0;
        border: none;
        border-radius: 10px;
        transition: $transition-2s;
        &:hover {
            background-color: #64b6e3;
        }
        &:focus {
            background-color: #64b6e3;
        }
    }
}


.cars-content-2 {
    width: 100%;
    margin: 0 0 0 auto;

    & .cars-c-2-container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 30px;
    }

}


// cards
.cars-card-item {
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 0 4px 0 rgba(62, 166, 223, 0.3);

    & > figure {
        width: 100%;
        margin: 0;
        border-radius: 15px 15px 0 0;
        border-bottom: 1px solid #d9d9d9;
        cursor: pointer;
        & > img {
            width: 100%;
            border-radius: 15px 15px 0 0;
        }
    }

    & .cars-card-item-body {
        padding: 10px 10px 15px;

        & > .cars-card-item-brand {
            color: $color-blue;
            font-size: 12px;
            font-weight: 600;
            margin: 0 0 10px;
        }

        & > .cars-card-item-title {
            color: $color-blue;
            font-size: 14px;
            font-weight: bold;
            margin: 0 0 10px;
        }


        & > .cars-card-item-country {
            color: #989898;
            font-size: 12px;
            font-weight: normal;

            & > .cars-card-item-country-icon {
                color: #989898;
                font-size: 17px;
                margin-right: 8px;

                & path {
                    stroke: #989898;
                }
            }
        }
    }
}





@media screen and (min-width: 1901px) {
    .cars-hero-container {
        padding: 180px 0;
    }

    
    .cars-content-2 {
        width: 90%;
        margin: 0 0 0 auto;

        & .cars-c-2-container {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 40px;
        }

    }

}


