            border-radius: 8px;
            box-shadow: 0 0 5px 0 rgba(62, 166, 223, 0.3);

            
        }

        & .selected-item {
            color: $color-blue;
            font-weight: bold;
        }

        &.cfmi-type-2 {
            grid-template-columns: 1fr;

            & button {
                padding: 10px;
                text-align: left;
            }
        }

        & h6 {
            color: $color-blue;
            font-size: 13px;
            margin-bottom: 10px;
        }
    }

    .cars-filter-modal-footer {

        & button {
            color: #fff;
            background-color: $color-lightblue;
            font-size: 12px;
            padding: 10px 24px;
            border: none;
            border-radius: 8px;
            outline: none;

            &:hover {
                background-color: $color-blue;
            }
        }
    }


}