
$color-blue: #182b89;
$color-lightblue: #3ea6df;

$transition-2s: all 0.2s ease-in-out;


// hero
.wrap-sgcars-hero {
    width: 100%;
    height: 100vh;
    margin: -80px auto 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 1), rgba(24, 43, 136, 0.1),rgba(24, 43, 136, 0.3),rgba(24, 43, 136, 0.5),rgba(24, 43, 136, 0.6), rgba(24, 43, 136, 0.7)), url('../../images/BG-38-01.png');
    background-repeat: no-repeat;
    background-position: center center; 
    background-size: cover;
    display: flex;
    align-items: center;

}


.sgcars-header {
    width: auto;
    margin: 0 0 60px 500px;
    padding: 0 0 0 20px; 
    border-left: 20px solid $color-blue;

    & .sgcars-breadcrumbs {
        display: flex;

        & a {
            color: #aeaeae;
            font-size: 24px;
            font-weight: bold;
            letter-spacing: 0.5px;
            text-decoration: none;
            transition: $transition-2s;
            &:hover {
                color: $color-blue;
            }
        }

        & .sgcars-breadcrumbs-divider {
            color: #aeaeae;
            margin: 0 10px;
            font-size: 24px;
        }

        & h2 {
            color: $color-blue;
            font-size: 24px;
            font-weight: bold;
            letter-spacing: 0.5px;
            margin: 0;
            line-height: initial;
        }
    }
    
}


.sgcars-container-1 {
    width: 100%;

    & figure {
        background-color: #fff;
        width: 100%;
        height: 300px;
        text-align: center;
        border-radius: 20px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);

        & > img {
            width: auto;
            height: 100%
        }
    }
}

.sgcars-item-header {
    margin: 0 0 30px;
    & > p {
        color: $color-blue;
        font-size: 18px;
        font-weight: normal;
        margin: 0 0 10px;
    }
    
    & > h4 {
        color: $color-blue;
        font-size: 24px;
        font-weight: bold;
        margin: 0 0 10px;
    }

    & > .cars-card-item-country {
        color: #989898;
        font-size: 18px;
        font-weight: normal;

        & > .cars-card-item-country-icon {
            color: #989898;
            font-size: 25px;
            margin-right: 8px;

            & path {
                stroke: #989898;
            }
        }
    }
}

.sgcars-item-attribute {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 15px;

    & .sgcars-attri_item {
        padding: 20px 10px;
        text-align: center;
        border-radius: 15px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) ;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5);

        & > img {
            width: 40px;
            height: 40px;
            margin: 0 auto 10px;
        }

        & > p {
            color: $color-blue;
            font-size: 12px;
            font-weight: bold;
            margin: 0 auto 5px;
        }

        & > h5 {
            color: #989898;
            font-size: 11px;
            font-weight: normal;
        }
    }
}

.sgcars-item-div {
    margin: 30px 0;
}

.sgcars-item-description {

    & > h5 {
        color: $color-blue;
        font-size: 18px;
        font-weight: bold;
        margin: 0 0 10px;
    }

    & > p {
        color: $color-blue;
        font-size: 14px;
        font-weight: normal;
        margin: 0 0 10px;
    }
}

.sgcars-loan-calculator {
    & input {
        width: 100%;
        color: $color-blue;
        background-color: #fff;
        font-size: 14px;
        font-weight: bold;
        padding: 0 10px;
        border: none;
        border-radius: 10px;
        outline: 0;
    }
    & input::-webkit-outer-spin-button,
    & input::-webkit-inner-spin-button { /* Chrome, Safari, Edge, Opera */
    -webkit-appearance: none;
    margin: 0;
    }
    & input[type=number] { /* Firefox */
    -moz-appearance: textfield;
    }
    & input::-webkit-input-placeholder { /* Edge */
        color: #989898;
        font-size: 14px;
        font-weight: normal;
    }  
    & input:-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: #989898;
        font-size: 14px;
        font-weight: normal;
    } 
    & input::placeholder {
        color: #989898;
        font-size: 14px;
        font-weight: normal;
    }

}

.sgcars-loan-sec-1 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 40px ;
    margin: 0 0 30px;
}   

.sgcars-loan-item-1 {

    & p {
        color: $color-blue;
        font-size: 14px;
        font-weight: bold;
        text-transform: capitalize;
        margin-bottom: 10px;
    }
    
    & div {
        color: $color-blue;
        display: flex;
        align-items: center;
        padding: 10px 20px;
        font-size: 14px;
        font-weight: bold;
        border-radius: 10px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
    }

    & .sgcars-loan-item-1-1 {
        justify-content: flex-start;
    }
    & .sgcars-loan-item-1-2 {
        & input {
            text-align: right;
        }
    }
    & .sgcars-loan-item-1-3 {
        & p {
            margin: 0 0 0 10px;
            font-size: 14px;
        }


    }
}


.sgcars-loan-sec-2 {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
}

.sgcars-loan-item-2 {
    & p {
        color: $color-blue;
        font-size: 14px;
        font-weight: bold;
        text-transform: capitalize;
        margin-bottom: 10px;
    }
    & input {
        padding: 10px 20px;
        font-size: 14px;
        font-weight: bold;
        border-radius: 10px;
        box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -moz-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
        -webkit-box-shadow: 0 0 8px 0 rgba(62, 166, 223, 0.5) !important;
    }
}

.sgcars-calculate-btn {
    color: #fff;
    background-color: $color-lightblue;
    width: 100%;
    padding: 12px 0;
    margin: 40px 0 0 ;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    outline: 0;
    border: none;
    border-radius: 15px;
    transition: $transition-2s;
    &:hover {
        background-color: #64b6e3;
    }
}


.canvas_chart {
    cursor: pointer !important;
}


@media screen and (min-width: 1901px) {

    .sgcars-header {
        margin: 0 0 60px 540px;
    }

    .sgcars-item-attribute {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 20px;
    
        & .sgcars-attri_item {
            & > h5 {
                color: #989898;
                font-size: 12px;
                font-weight: normal;
            }
        }
    }
}


@media screen and (max-width: 1200px) {
    .wrap-sgcars-hero {
        height: 800px;
    }

    .cars-container-1 {
        width: 90%;
        grid-template-columns: 1fr 2fr;
        gap: 40px;
    }

    
    .sgcars-header{
        width: 90%;
        margin: 0 auto 30px;

        & .sgcars-breadcrumbs {
            flex-wrap: wrap;

            & a {
                font-size: 20px;
            }
            & h2 {
                font-size: 20px;
            }
        }
    }

}



@media screen and (max-width: 1024px) {
    .wrap-sgcars-hero {
        height: 700px;
    }

    .cars-container-1 {
        width: 90%;
        grid-template-columns: 1fr;
    }
}



@media screen and (max-width: 576px) {


    .sgcars-item-header  {

        & > p {
            font-size: 16px;
            margin-bottom: 0px;
        }

        & > h4 {
            font-size: 20px;
        }

        & > .cars-card-item-country {
            font-size: 14px;

            & > .cars-card-item-country-icon {
                font-size: 20px;
            }
        }
    }

    .sgcars-container-1 figure {
        width: 90%;
        height: 200px;
        margin: 20px auto;
    }

    .sgcars-item-attribute {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 10px;

        & .sgcars-attri_item {
            padding: 15px 10px 10px;

            & > img {
                width: 30px;
                height: 30px;
            }
            & > p {
                font-size: 9px;
            }
            & > h5 {
                font-size: 9px;
            }
        }
    }

    .sgcars-item-description  {

        & > h5 {
            font-size: 16px;
        }
        & > p {
            font-size: 12px;
        }
    }
}