$background: #f8f8f8;

$FontColor-White: #fff;
$FontColor-Green: #182b89;


.PageError {
    width: auto;
    height: 100vh;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -80px 0 0;
}

.PageError > h1 {
    color: $FontColor-Green;
    font-size: 40px;
    margin: 0 30px 0 0;
    padding-right: 30px;
    border-right: 1px solid #182b89;
}

.PageError > div > p {
    color: $FontColor-Green;
    font-size: 16px;
    font-weight: bold;
    margin: 0 0 40px;
}

.b-to-homepage-btn {
    font-size: 14px;
    font-weight: bold;
    color: $FontColor-White;
    background-color: #182b89;
    text-decoration: none;
    padding: 12px 40px;
    border: 2px solid #182b89;
    border-radius: 8px;
    transition: all 0.3s ease-in-out;
}

.b-to-homepage-btn:hover {
    color: $FontColor-White;
    background-color: #3ea6df;
    border: 2px solid #3ea6df;
   
}




@media screen and (max-width: 576px) {
    .PageError {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .PageError > h1 {
        font-size: 36px;
        border: none;
        margin: 0 0 30px;
        padding: 0;
    }

    .PageError > div {
        width: 90%;
        text-align: center;
    }
}